




























































































































































































































































































































































































































































































































































.repor-cont {
    background: #fff;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
    border-radius: 6px;
}

.common-font {
    .el-form--inline {
        flex: 1;
        display: flex;
        flex-flow: wrap;
    }

    .btn-box {
        position: absolute;
        right: 0;
    }




    /deep/ .el-range-separator {
        width: 8%;
    }
}


.commpage {
    width: 100%;
    overflow: hidden;

    ::v-deep .el-card__header {
        padding: 0.16rem 0.13rem 0.16rem 0.4rem;
    }

    ::v-deep .el-card__header {
        position: relative;
    }

    ::v-deep .el-card__header::after {
        content: "";
        position: absolute;
        background: #2e4af7;
        left: 0.24rem;
        top: 0.18rem;
        width: 6px;
        height: 0.2rem;
    }

    .clearfix {
        color: #333333;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.16rem;
    }

    ::v-deep .el-card__body {
        padding: 0;
    }
}

.common-card {
    padding: 0.2rem 0.23rem;
}

.el-form--inline .el-form-item {
    font-size: 0.14rem;
}

.reporSelect {
    width: 2.37rem;
    height: 0.32rem;
    margin-right: 0.3rem;

    ::v-deep .el-input__inner {
        line-height: 0.32rem;
        height: 0.32rem;
    }
}

.common-font {
    position: relative;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
}

.desc-box {
    min-width: 140px
}

.btn-box {
    position: absolute;
    right: 0;
}

.btn-box span {
    width: 65px;
    height: 32px;
    text-align: center;
    line-height: 32px;
    border-radius: 4px;
    display: inline-block;
    cursor: pointer;
}

.btn-box span:nth-child(1) {
    background: #2e4af7;
    color: #fff;
    margin-right: 8px;
}

.btn-box span:nth-child(2) {
    background: #fff;
    color: #333333;
    border: 1px solid #e3e5ec;
}

.reporTabs {
    font-size: 14px;
}

.reporTabs span {
    width: 65px;
    height: 32px;
    text-align: center;
    margin-right: 8px;
    line-height: 32px;
    border-radius: 4px;
    display: inline-block;
    cursor: pointer;
    background: #fff;
    color: #333333;
    border: 1px solid #e3e5ec;
}

.reportCheckTab {
    background: #2e4af7;
    color: #fff;
    border: 1px solid #2e4af7;
}

.checked-btn {
    background: #2e4af7 !important;
    color: #fff !important;
    border: 1px solid #2e4af7 !important;
}

.desc-box {
    margin-top: 0.22rem;
    padding: 0.09rem 0.23rem;
    font-size: 0.14rem;
    background: rgba(46, 74, 247, 0.03);
    border-radius: 2px;
    border: 1px solid rgba(46, 74, 247, 0.31);
}

.desc-cont {
    display: flex;
    margin-top: 5px;
    // justify-content: space-between;
    color: rgba(0, 0, 0, 0.65);

    .felxOne {
        flex: 1;
    }

    .felxThree {
        flex: 3;
    }
}




.desc-box div:nth-child(5) {
    width: 2.75rem;
}

.bule {
    color: #2e4af7;
    border-bottom: 2px solid #2e4af7;
}

.light-bule {
    color: #8090f8;
    border-bottom: 2px solid #8090f8;
}

.violet {
    color: #7937e3;
    border-bottom: 2px solid #7937e3;
}

.yellow {
    color: #ff8446;
    border-bottom: 2px solid #ff8446;
}

.red {
    color: #ff2b73;
    border-bottom: 2px solid #ff2b73;
}

.table-box {
    margin-top: 0.22rem;
}

.report-pick {
    ::v-deep .el-range-editor.el-input__inner {
        padding: 0 10px;
    }

    ::v-deep .el-date-editor .el-range-separator {
        padding: 0 5px;
        line-height: 29px;
        width: 5%;
        color: #303133;
    }

    ::v-deep .el-input__inner {
        line-height: 0.32rem;
        height: 0.32rem;
    }
}

.repbarChar {
    height: 300px;
}

.point-item {
    ::v-deep .el-form-item__content {
        width: 4rem;

        .el-select {
            width: 100%;
        }
    }
}

.min-item,
.max-item {
    ::v-deep .el-form-item__label {
        text-align: left;
    }
}

.max-item {

    ::v-deep .el-form-item__content {
        .el-date-editor .el-range-input {
            width: 42%;
        }
    }
}
